$card: #FFDB58;
.card {
    width: 100px;
    height: 100px;
    
    
    
    
    .spin {
        width: 100%;
        height: 100%;

        .card-back {
            background-image: url("./img/back.svg");
            background-repeat: no-repeat;
            background-color: $card;
            width: 100%;
            height: 100%;
            border: 0.3rem solid #000;
            border-radius: 5px;
            padding: 1%;
        }
    }

    
}



